.st-splitter-vertical {
  display: flex;
  flex-direction: row;
}
.st-splitter-vertical > hr {
  border: 3px solid #ccc;
  margin: 0;
  cursor: w-resize;
}
.st-splitter-vertical > :not(hr) {
  overflow: hidden;
}
.st-splitter-vertical > :not(hr):not(:last-child) {
  flex: 0 0 auto;
}
.st-splitter-vertical > :not(hr):last-child {
  flex: 1 1 auto;
}

.st-splitter-vertical--fixed > :not(hr):not(:last-child) {
  flex: 0 11 auto;
}

.st-splitter-horizontal {
  display: flex;
  flex-direction: column;
}
.st-splitter-horizontal > hr {
  border: 3px solid #ccc;
  margin: 0;
  width: 100%;
  cursor: n-resize;
}
.st-splitter-horizontal > :not(hr) {
  overflow: hidden;
}

.st-splitter-horizontal--fixed {
  height: 100%;
}
.st-splitter-horizontal--fixed > :not(hr):last-child {
  height: 100%;
}

.st-splitter-table th {
  position: relative;
}
.st-splitter-table th > hr {
  position: absolute;
  top: 0;
  left: -3px;
  border: 0;
  margin: 0;
  width: 6px;
  height: 100%;
  cursor: w-resize;
}